#header__cont {
  width: 100%;
  height: auto;
  position: relative;
}
.header__appbar {
  width: 100%;
  height: 10vh;
  background-color: #FAFAFA;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 1.3em;
  color: #333333;
  -webkit-box-shadow: 0px 2px 6px rgba(0,0,0,0.28);
          box-shadow: 0px 2px 6px rgba(0,0,0,0.28);
}
.header__title {
  text-align: left;
  margin: 0;
  margin-left: 10px;
  font-weight: 700;
  font-family: "Roboto", "San Francisco", "Verdana", sans-serif;
}
.header__right {
  position: absolute;
  text-align: right;
  right: 0;
  margin-right: 30px;
  color: #757575;
}
.header__link {
  text-decoration: none;
  color: #757575;
}
.header__hamburger {
  display: none;
  cursor: pointer;
  color: #212121;
}

/* ----------------------- */
.ct__cont {
  width: auto;
  /*max-width: 94%;*/
  height: auto;
  margin: 40px;
  text-align: left;
  color: #757575;
  /*opacity: 0;*/
  font-family: "Playfair Display", serif;
}
.ct__title {
  color: #333333;
  font-size: 2.2em;
  font-weight: 700;
  margin-bottom: 20px;
}
.ct__p {
  font-size: 1.1em;
  line-height: 1.5;
}
@media screen and (max-width: 414px) {
  .header__hamburger {
    display: inline-block;
  }
  .header__links {
    display: none;
  }
}


